import React from 'react';
import Icon from '../icon';
import classnames from '../utils/classnames';
import { ButtonProps }from './interface'



const Button = ({
  type = 'primary',
  size = 'm',
  icon,
  children,
  onClick,
  width,
  loading = false,
  disabled = false,
  htmlType,
  className,
  style,
  ...rest
}: ButtonProps) => (
  <button
    type={htmlType}
    onClick={e => !loading && onClick && onClick(e)}
    className={classnames(`button button-${type} button-${size}`, className, {
      loading,
    })}
    style={{ ...style, width }}
    disabled={disabled}
    {...rest}
  >
    <span className="button__inner">
      {(loading || icon) && (
        <Icon
          name={loading ? 'loading' : icon}
          className="button-icon"
          style={{
            marginRight: children ? 8 : 0,
          }}
        />
      )}
      {children && <span className="button-text">{children}</span>}
    </span>
  </button>
);

export default Button;
