import React from 'react';
import { IconProps } from './interface';

const Icon: React.FC<IconProps> = (props) => {
  const { type, name, size, ...otherProps } = props;
  const svg = (type || name || '')
    .split('/')
    .filter((p: string) => !!p)
    .pop();
  return (
    <svg width="1em" height="1em" fill="currentcolor" {...otherProps}>
      <use xlinkHref={`#${svg}`} />
    </svg>
  );
};
export default Icon;
